.OrderListElement{
    transition: 0.5s;
}
.OrderListElement .head{
    width: 100%;
    height: 30px;
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    padding:0 20px;
    align-items: center;
    background-color:#EFF3F6;
    font-family: 'Roboto', sans-serif;
    color: #707070;
}
.head span:nth-child(2){
    color: #085076;
}
.OrderListElement:hover{
    background-color: #EFF3F6;
}