.InputTag{ 
    background-color: white;
    position: relative;
    font-family: 'Roboto',sans-serif;
    display: inline-block;
    margin:5px 0;
}
.InputTag input:focus, .InputTag textarea:focus{
    border:1px solid #085076;
    box-shadow: 0px 3px 5px 0px rgb(189, 189, 189);
}
.InputTag input, .InputTag textarea{
    width: 100%;
    height: 100%;
    font-family: 'Roboto',sans-serif;
    background: none;
    border: none;
    text-align: left;
    outline: none;
    padding: 0;
    transition: 0.5s;
    padding-left:5px ;
}
.InputTag label{
    position: absolute;
    top:-30px;
    left: 0;
    font-weight: 700;
    color:#464646;
    white-space: nowrap;;
}