.OrdersBar{
    height: calc( 100% - 20px ) ;
    width: calc( 60% - 20px ) ;
    position: absolute;
    left: 10px;
    top: 10px;
    background-color: white;
    border-radius: 7px;
    overflow: hidden;
}
.list{
    height:calc( 100% - 100px ) ;
    overflow-y: scroll;
}
::-webkit-scrollbar{
    display:none;
}