.SellerSubHead{
    width:100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-family: 'Roboto', sans-serif;
    list-style: none;
    padding: 0;
    color: #707070;
    font-weight: 700;
    box-sizing: border-box;
    padding:0 10px; 
    height: 30px;
}
.SellerSubHead li{
    /* text-align: center; */
}
.SellerSubHead li:nth-child(1),.SellerSubHead li:nth-child(5),.SellerSubHead li:nth-child(6){
    width: 13%;
}
.SellerSubHead li:nth-child(5),.SellerSubHead li:nth-child(6){
    text-align: center;
}
.SellerSubHead li:nth-child(3),.SellerSubHead li:nth-child(4){
    width: 17%;
    text-align: center;
}
.SellerSubHead li:nth-child(2){
    width: 25%;
    text-align: start;
}