.SellerDetails{
    padding-bottom: 30px;
    text-align:left;
}
.SellerDetails h1{
    font-size:19px;
    font-family: 'Josefin Sans', sans-serif;
    color: #085076;
}
.imageList{
    display: flex;
    width: 100%;
    overflow-y:auto;
}
.imageList div{
    margin-right: 10px;
}
.SoldBy{
    margin:15px 0;
}
.Bold{
    font-weight: 700;
    color: #464646;
    font-family: 'Roboto',sans-serif;
    font-size: 15px;
    margin:15px 0;
}
.Filters > div{
    display: inline-block;
    width: 49%;
}
.Basic{
    width: 100%;
    display: flex;
    justify-content: space-around;
    margin:50px 0;
}
.Inventory{
    width: 100%;
    margin:50px 0;
}
.Inventory > div{
    margin:0px 10px;
}
.Inventory > div input{
    text-align: center;
}
.LeftAlign{
    width: 80%;
    display: flex;
    justify-content: space-between;
    margin:50px 0;
}
.add{
    padding:0px 5px;
    border-radius: 50%;
    display: inline-block;
    background-color: white;
    border:1px solid #085076;
}
.SellerDetails .buttonCont{
    display:flex;
    justify-content: space-evenly;
}
.SellerDetails button{
    padding:5px 25px;
    display: block;
    margin:auto;
    font-family: 'Josefin Sans', sans-serif;
    background-color: #085076;
    color: white;
    outline: none;
}
.customer h3{
    color: #085076;
    font-weight: 100;
    font-family: 'Roboto', sans-serif;
    margin-top: 8px;
    font-size: 17px;
    margin-bottom: 4px;
}
.customer h2{
    font-size: 15px;
    color: #707070;
}
.customer p{
    margin:3px 0;
    width: 80%;
    font-size: 16px;
    color: #707070;
    font-family: 'Roboto', sans-serif;
}
