.OrderSubHead{
    width:100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-family: 'Roboto', sans-serif;
    list-style: none;
    padding: 0;
    color: #707070;
    font-weight: 700;
    box-sizing: border-box;
    padding:0 10px; 
    height: 30px;
}
.OrderSubHead li{
    text-align: center;
}
.OrderSubHead li:nth-child(1),.OrderSubHead li:nth-child(6),.OrderSubHead li:nth-child(7){
    width: 11%;
}
.OrderSubHead li:nth-child(5){
    width: 17%;
}
.OrderSubHead li:nth-child(4),.OrderSubHead li:nth-child(2){
    width: 20%;
    /* text-align: start; */
}
.OrderSubHead li:nth-child(3){
    width: 9%;
}