.SellerListElement{
    width:100%;
    height: 100px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-family: 'Roboto', sans-serif;
    font-weight: 100;
    list-style: none;
    padding: 0;
    color: #707070;
    box-sizing: border-box;
    padding:0 10px; 
    transition: 0.5s;
}
.SellerListElement:hover{
    background-color: #EFF3F6;
}
.SellerListElement li{
    text-align: center;
    position: relative;
}
.SellerListElement li:nth-child(1),.SellerListElement li:nth-child(5),.SellerListElement li:nth-child(6){
    width: 13%;
}
.SellerListElement li:nth-child(3),.SellerListElement li:nth-child(4){
    width: 17%;
}
.SellerListElement li:nth-child(2){
    width: 25%;
    text-align: start;
}