nav{
    position: fixed;
    top: 0;
    left: 0;
    height: 7vh;
    width: 100%;
    display: flex;
    align-items: center;
    color:  #085076;
    background-color: white;
    box-shadow: 0px 3px 20px #dddddd;
    font-family: 'Josefin Sans', sans-serif;
    font-weight: 100;
    z-index: 100;
    
}
nav .logo{
    width:7%;
    box-sizing: border-box;
    padding-left: 30px;
    white-space: nowrap;
    font-size: 1.5em;
    
}
nav .logo img{
    width: 100%;
    height: 100%;
}
nav ul{
    display: flex;
    list-style: none;   
    justify-content: space-evenly; 
}
nav > ul{
    width:90% ;
}
nav > ul > li {
    width: 75%;
    
}
nav > ul >li.login{
    width:11%;
    text-align: right;
    cursor: pointer;
} 
nav > ul > li.login div{
    position: relative;
    display: inline;
}
nav ul li ul li{
    position: relative;
    cursor: pointer;
}
nav a{
    color:inherit;
    text-decoration: none;
}
nav ul li span, .login span{
    width: 0%;
    height: 2px;
    background-color: #085076;
    position: absolute;
    bottom: -5px;
    left: 0;
    transition: 0.5s;
}
nav ul li:hover >a span, .login:hover  span{
    width: 50%;
}
.active span{
    width: 100% !important;
}
nav ul li.active span{
    width: 100% !important;
}
.hamburger{
    height: 2.5vh;
    /* margin:2.4vh 10px; */
    display: none;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
}
.hamburger div{
    width: 23px;
    height: 3px;
    background-color: #085076;
    border-radius: 15px;
    transition: all 0.5s ease;
}

.ham-active.hamburger .line1{
    transform:translate(0px,1vh) rotateZ(45deg);
    width: 23px;
    background-color: #085076;
}
.ham-active.hamburger .line2{
    opacity: 0;
}
.ham-active.hamburger .line3{
    width: 23px;
    transform:translate(0px,-1vh) rotateZ(-45deg) ;
    background-color: #085076;
}