.DropDown{
    width: 95%;
    /* margin:0 5%; */
    display: flex;
    justify-content: space-between;
    height: 30px;
    align-items: center;
    margin:10px 0;
}
.DropDown select{
    width: 60%;
    height: 100%;
    color: #085076;
    box-sizing: border-box;
    padding: 0 5px;
    border:none;
    outline: none;
}