.CustomerListElement{
    width:100%;
    height: 100px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-family: 'Roboto', sans-serif;
    font-weight: 100;
    list-style: none;
    padding: 0;
    color: #707070;
    box-sizing: border-box;
    padding:0 10px; 
    transition: 0.5s;
}
.CustomerListElement:hover{
    background-color: #EFF3F6;
}
.CustomerListElement li{
    /* text-align: center; */
    position: relative;
}
.CustomerListElement li:nth-child(1),.CustomerListElement li:nth-child(4),.CustomerListElement li:nth-child(5),.CustomerListElement li:nth-child(6),.CustomerListElement li:nth-child(7){
    width: 12%;
    text-align: center;
}
.CustomerListElement li:nth-child(2){
    width: 23%;
    text-align: start;
}
.CustomerListElement li:nth-child(3){
    width: 16%;
    text-align: start;
}