.SubHead{
    width:100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-family: 'Roboto', sans-serif;
    list-style: none;
    padding: 0;
    color: #707070;
    font-weight: 700;
    box-sizing: border-box;
    padding:0 10px; 
    height: 30px;
}
.SubHead li{
    /* text-align: center; */
}
.SubHead li:nth-child(1),.SubHead li:nth-child(4),.SubHead li:nth-child(5),.SubHead li:nth-child(6),.SubHead li:nth-child(7){
    width: 12%;
    text-align: center;
}
.SubHead li:nth-child(2){
    width: 23%;
    text-align: start;
}
.SubHead li:nth-child(3){
    width: 16%;
    text-align: start;
}