.OrderProductElement{
    width:100%;
    height: 100px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-family: 'Roboto', sans-serif;
    font-weight: 100;
    list-style: none;
    padding: 0;
    color: #707070;
    box-sizing: border-box;
    padding:0 10px; 
    transition: 0.5s;
}

.OrderProductElement li{
    text-align: center;
    position: relative;
}
.OrderProductElement li:nth-child(1),.OrderProductElement li:nth-child(6),.OrderProductElement li:nth-child(7){
    width: 11%;
}
.OrderProductElement li:nth-child(5){
    width: 17%;
}
.OrderProductElement li:nth-child(4){
    width: 20%;
    text-align: center;
    font-size: 13px;
}
.OrderProductElement li:nth-child(2){
    width: 20%;
    text-align: start;
    font-size: 13px;
}
.OrderProductElement li:nth-child(3){
    width: 9%;
}