.SmallImage{
    width: 60px;
    height: 70px;
    background-color: white;
    overflow: hidden;
    position: relative;
}
.SmallImage img{
    width: 100%;
    height: 100%;
}
.SmallImage svg{
    width:25px;
    height:25px;
    margin:22.5px 17.5px; 
}
.Delete{
    position: absolute;
    top:2px;
    right:-6px;
    background-color: rgba(226, 27, 27, 0.795);
    width: 25px;
    height: 25px;
    display: none;
    justify-content: space-evenly;
    align-items: center;
    border-radius: 7px;
}
.Delete{
    color:white;
    font-size: 0.8em;
}
.SmallImage:hover > .Delete{
    display:flex;
}