.ToolBar{
    width: 100%;
    box-sizing: border-box;
    padding: 0 20px;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.head{
    color:#085076;
    font-family: 'Josefin Sans', sans-serif;
    font-size: 21px;
}
.button{
    height: 25px;
    width: 25px;
    border-radius: 7px;
    background-color: #085076;
    position: relative;
    outline: none;
    border: none;
}
.button svg{
    color:white;
    position: absolute;
    top:50%;
    left:50%;
    transform:translate(-50%,-50%);
}
.button line{
    stroke: white;
}
.dropdowns{
    display: flex;
    width: 30%;
    height: 100%;
    justify-content: space-around;
    align-items: center;
}
.dropdowns label{
    display:none;
}