.ProductListElement{
    width:100%;
    height: 100px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-family: 'Roboto', sans-serif;
    font-weight: 100;
    list-style: none;
    padding: 0;
    color: #707070;
    box-sizing: border-box;
    padding:0 10px; 
    transition: 0.5s;
}
.ProductListElement:hover{
    background-color: #EFF3F6;
}
.ProductListElement li{
    /* text-align: center; */
    position: relative;
}
.ProductListElement li:nth-child(1),.ProductListElement li:nth-child(6){
    width: 11%;
    text-align: center;
}
.ProductListElement li:nth-child(2),.ProductListElement li:nth-child(3),.ProductListElement li:nth-child(5){
    width: 21%;
    text-align: start;
}
.ProductListElement li:nth-child(4){
    width: 9%;
    
}