.CustomerDetails h1{
    font-size:19px;
    font-family: 'Josefin Sans', sans-serif;
    color: #085076;
    margin-top: 30px;
}
.CustomerDetails h3{
    color: #707070;
    font-size:16px;
    font-family: 'Roboto', sans-serif;
}
.total{
    font-size:18px;
    font-family: 'Roboto', sans-serif;
}
.orderDetail{
    border-bottom: 3px dotted gray;
}
.products > div{
    display: flex;
    width: 60%;
    justify-content: flex-start;
    margin:20px 0;
    align-items: center;
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    color: #707070;
}
.products > div > div{
    margin-left:30px;
}
.products > div > div div{
    padding:3px 0;
}
.products span{
    margin-right: 20px;
    color:black;
}
.customer h3{
    color: #085076;
    font-weight: 100;
    font-family: 'Roboto', sans-serif;
    margin-top: 8px;
    font-size: 17px;
    margin-bottom: 4px;
}
.customer h2{
    font-size: 15px;
    color: #707070;
}
.customer p{
    margin:3px 0;
    width: 80%;
    font-size: 16px;
    color: #707070;
    font-family: 'Roboto', sans-serif;
}
.buttons{
    display: flex;
    justify-content: space-between;
    margin:20px 0;
}
#cancel{
    background-color: white;
    color:#085076;
}
.customer button{
    padding:10px 45px;
    font-family: 'Josefin Sans', sans-serif;
    border:1px solid #085076;
    color:white;
    background-color: #085076;
    min-width: 45%;
    margin:auto;
    display:block;
    transition: 0.5s;
    outline:none;
}
.customer button:hover{
    box-shadow: 0px 0px 10px #0850769a;
}
.CustomerDetails{
    padding-bottom: 50px;
}