.Modal{
    position: absolute;
    top:0;
    width: 100%;
    height: 100%;
    left: 0;
    background-color: rgba(0,0,0,0.5);
}
.form{
    max-width: 70%;
    min-width: 300px;
    min-height:100px ;
    text-align: center;
    max-height: 90%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    background-color:#EFF3F6;
    overflow: auto;
    padding: 5px 10px;
    border-radius: 7px;
}